'use client';

import { useQuery } from '@tanstack/react-query';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { visitsApi, patientsApi, nursesApi, incidentsApi } from '@/services/api-client';
import { formatDate, formatTime } from '@/lib/utils';
import { 
  Users, 
  UserCheck, 
  Calendar, 
  AlertTriangle, 
  Plus,
  Eye,
  Clock,
  Activity,
  User
} from 'lucide-react';
import Link from 'next/link';

export default function DashboardPage() {
  const { data: visits, isLoading: visitsLoading } = useQuery({
    queryKey: ['visits'],
    queryFn: () => visitsApi.getAll(),
  });

  const { data: patients, isLoading: patientsLoading } = useQuery({
    queryKey: ['patients'],
    queryFn: () => patientsApi.getAll(),
  });

  const { data: nurses, isLoading: nursesLoading } = useQuery({
    queryKey: ['nurses'],
    queryFn: () => nursesApi.getAll(),
  });

  const { data: incidents, isLoading: incidentsLoading } = useQuery({
    queryKey: ['incidents'],
    queryFn: () => incidentsApi.getAll(),
  });

  if (visitsLoading || patientsLoading || nursesLoading || incidentsLoading) {
    return (
      <div className="space-y-6">
        <div className="flex items-center justify-between">
          <h1 className="text-2xl font-bold text-gray-900">Dashboard</h1>
        </div>
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
          {[1, 2, 3, 4].map((i) => (
            <Card key={i}>
              <CardContent className="p-6">
                <div className="animate-pulse">
                  <div className="h-4 bg-gray-200 rounded w-3/4 mb-2"></div>
                  <div className="h-8 bg-gray-200 rounded w-1/2"></div>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      </div>
    );
  }

  const todayVisits = visits?.filter(visit =>
    new Date(visit.date).toDateString() === new Date().toDateString()
  ) || [];

  const recentIncidents = incidents?.sort((a, b) =>
    new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime()
  ).slice(0, 5) || [];

  const activeNurses = nurses?.filter(nurse => nurse.status === 'active') || [];

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">Dashboard</h1>
          <p className="text-gray-600 mt-1">Welcome back! Here's what's happening today.</p>
        </div>
      </div>

      {/* Quick Actions */}
      <div className="flex flex-wrap gap-4">
        <Button asChild variant="primary" size="lg" className="shadow-lg">
          <Link href="/visits">
            <Plus className="h-5 w-5 mr-2" />
            New Visit
          </Link>
        </Button>
        <Button asChild variant="outline" size="lg">
          <Link href="/patients">
            <User className="h-5 w-5 mr-2" />
            Add Patient
          </Link>
        </Button>
        <Button asChild variant="outline" size="lg">
          <Link href="/nurses">
            <UserCheck className="h-5 w-5 mr-2" />
            Add Nurse
          </Link>
        </Button>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <Card>
          <CardContent className="p-6">
            <div className="flex items-center">
              <div className="p-2 bg-blue-100 rounded-lg">
                <Users className="h-6 w-6 text-blue-600" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600">Total Patients</p>
                <p className="text-2xl font-bold text-gray-900">{patients?.length || 0}</p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-6">
            <div className="flex items-center">
              <div className="p-2 bg-green-100 rounded-lg">
                <UserCheck className="h-6 w-6 text-green-600" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600">Active Nurses</p>
                <p className="text-2xl font-bold text-gray-900">{activeNurses.length}</p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-6">
            <div className="flex items-center">
              <div className="p-2 bg-purple-100 rounded-lg">
                <Calendar className="h-6 w-6 text-purple-600" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600">Today's Visits</p>
                <p className="text-2xl font-bold text-gray-900">{todayVisits.length}</p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-6">
            <div className="flex items-center">
              <div className="p-2 bg-red-100 rounded-lg">
                <AlertTriangle className="h-6 w-6 text-red-600" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600">Open Incidents</p>
                <p className="text-2xl font-bold text-gray-900">{incidents?.filter(i => i.status === 'open').length || 0}</p>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Today's Visits */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center">
            <Calendar className="h-5 w-5 mr-2" />
            Today's Visits
          </CardTitle>
          <CardDescription>
            Visits scheduled for today
          </CardDescription>
        </CardHeader>
        <CardContent>
          {todayVisits.length === 0 ? (
            <div className="text-center py-8 text-gray-500">
              <Calendar className="h-12 w-12 mx-auto mb-4 text-gray-300" />
              <h3 className="text-lg font-medium text-gray-900 mb-2">No visits today</h3>
              <p className="text-gray-500">No visits are scheduled for today.</p>
            </div>
          ) : (
            <div className="space-y-3">
              {todayVisits.map((visit) => (
                <div key={visit.id} className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
                  <div className="flex items-center space-x-4">
                    <div className="h-10 w-10 bg-blue-100 rounded-full flex items-center justify-center">
                      <span className="text-sm font-medium text-blue-700">
                        {formatTime(visit.windowStart).substring(0, 2)}
                      </span>
                    </div>
                    <div>
                      <h4 className="font-medium text-gray-900">
                        {patients?.find(p => p.id === visit.patientId)?.name || 'Unknown Patient'}
                      </h4>
                      <p className="text-sm text-gray-600">
                        {formatTime(visit.windowStart)} - {formatTime(visit.windowEnd)}
                      </p>
                    </div>
                  </div>
                  <div className="flex items-center space-x-2">
                    <span className="text-sm text-gray-900">
                      {visit.status.replace('_', ' ')}
                    </span>
                    <Button variant="ghost" size="sm">
                      <Eye className="h-4 w-4" />
                    </Button>
                  </div>
                </div>
              ))}
            </div>
          )}
        </CardContent>
      </Card>

      {/* Recent Incidents */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center">
            <AlertTriangle className="h-5 w-5 mr-2" />
            Recent Incidents
          </CardTitle>
          <CardDescription>
            Latest incidents that need attention
          </CardDescription>
        </CardHeader>
        <CardContent>
          {recentIncidents.length === 0 ? (
            <div className="text-center py-8 text-gray-500">
              <AlertTriangle className="h-12 w-12 mx-auto mb-4 text-gray-300" />
              <h3 className="text-lg font-medium text-gray-900 mb-2">No recent incidents</h3>
              <p className="text-gray-500">No incidents have been reported recently.</p>
            </div>
          ) : (
            <div className="space-y-3">
              {recentIncidents.map((incident) => (
                <div key={incident.id} className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
                  <div className="flex items-center space-x-4">
                    <div className="h-10 w-10 bg-red-100 rounded-full flex items-center justify-center">
                      <AlertTriangle className="h-5 w-5 text-red-600" />
                    </div>
                    <div>
                      <h4 className="font-medium text-gray-900">
                        {incident.category?.replace('_', ' ').toUpperCase() || 'INCIDENT'}
                      </h4>
                      <p className="text-sm text-gray-600">
                        {incident.description}
                      </p>
                    </div>
                  </div>
                  <div className="flex items-center space-x-2">
                    <span className="text-sm text-gray-500">
                      {formatDate(incident.createdAt)}
                    </span>
                    <Button variant="ghost" size="sm">
                      <Eye className="h-4 w-4" />
                    </Button>
                  </div>
                </div>
              ))}
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  );
}