// Optimized API delay - reduced for better performance
const delay = (ms: number = 100) => new Promise(resolve => setTimeout(resolve, ms));

// Cache for frequently accessed data
const cache = new Map<string, { data: any; timestamp: number }>();
const CACHE_DURATION = 5 * 60 * 1000; // 5 minutes

// Cache helper functions
const getCachedData = (key: string) => {
  const cached = cache.get(key);
  if (cached && Date.now() - cached.timestamp < CACHE_DURATION) {
    return cached.data;
  }
  return null;
};

const setCachedData = (key: string, data: any) => {
  cache.set(key, { data, timestamp: Date.now() });
};

// Generic CRUD operations
class MockApiClient<T extends { id: string }> {
  private data: T[];
  private storageKey: string;

  constructor(initialData: T[], storageKey: string) {
    this.storageKey = storageKey;
    this.data = this.loadFromStorage() || initialData;
  }

  private loadFromStorage(): T[] | null {
    if (typeof window === 'undefined') return null;
    try {
      const stored = localStorage.getItem(this.storageKey);
      return stored ? JSON.parse(stored) : null;
    } catch {
      return null;
    }
  }

  private saveToStorage(): void {
    if (typeof window === 'undefined') return;
    try {
      localStorage.setItem(this.storageKey, JSON.stringify(this.data));
    } catch {
      // Ignore storage errors
    }
  }

  async getAll(): Promise<T[]> {
    const cacheKey = `getAll_${this.storageKey}`;
    const cached = getCachedData(cacheKey);
    if (cached) {
      return cached;
    }
    
    await delay(50); // Reduced delay
    const result = [...this.data];
    setCachedData(cacheKey, result);
    return result;
  }

  async getById(id: string): Promise<T | null> {
    const cacheKey = `getById_${this.storageKey}_${id}`;
    const cached = getCachedData(cacheKey);
    if (cached) {
      return cached;
    }
    
    await delay(25); // Reduced delay
    const result = this.data.find(item => item.id === id) || null;
    setCachedData(cacheKey, result);
    return result;
  }

  private invalidateCache(): void {
    // Clear all cache entries for this storage key
    for (const key of cache.keys()) {
      if (key.includes(this.storageKey)) {
        cache.delete(key);
      }
    }
  }

  async create(item: Omit<T, 'id' | 'createdAt' | 'updatedAt'>): Promise<T> {
    await delay(100); // Reduced delay
    const newItem = {
      ...item,
      id: Math.random().toString(36).substr(2, 9),
      createdAt: new Date(),
      updatedAt: new Date(),
    } as unknown as T;
    
    this.data.push(newItem);
    this.saveToStorage();
    this.invalidateCache(); // Clear cache after modification
    return newItem;
  }

  async update(id: string, updates: Partial<T>): Promise<T | null> {
    await delay(75); // Reduced delay
    const index = this.data.findIndex(item => item.id === id);
    if (index === -1) return null;

    const updatedItem = {
      ...this.data[index],
      ...updates,
      updatedAt: new Date(),
    } as T;

    this.data[index] = updatedItem;
    this.saveToStorage();
    this.invalidateCache(); // Clear cache after modification
    return updatedItem;
  }

  async delete(id: string): Promise<boolean> {
    await delay(50); // Reduced delay
    const index = this.data.findIndex(item => item.id === id);
    if (index === -1) return false;

    this.data.splice(index, 1);
    this.saveToStorage();
    this.invalidateCache(); // Clear cache after modification
    return true;
  }

  async search(query: string, fields: (keyof T)[]): Promise<T[]> {
    const cacheKey = `search_${this.storageKey}_${query}_${fields.join(',')}`;
    const cached = getCachedData(cacheKey);
    if (cached) {
      return cached;
    }
    
    await delay(50); // Reduced delay
    const lowercaseQuery = query.toLowerCase();
    const result = this.data.filter(item =>
      fields.some(field => {
        const value = item[field];
        if (typeof value === 'string') {
          return value.toLowerCase().includes(lowercaseQuery);
        }
        return false;
      })
    );
    setCachedData(cacheKey, result);
    return result;
  }
}

