// Specialized API methods
export const visitsApiExtended = {
  ...visitsApi,
  
  async getByStatus(status: string[]): Promise<Visit[]> {
    await delay(100);
    const allVisits = await visitsApi.getAll();
    return allVisits.filter(visit => status.includes(visit.status));
  },

  async getByDateRange(startDate: Date, endDate: Date): Promise<Visit[]> {
    await delay(100);
    const allVisits = await visitsApi.getAll();
    return allVisits.filter(visit => 
      visit.date >= startDate && visit.date <= endDate
    );
  },

  async getByNurse(nurseId: string): Promise<Visit[]> {
    await delay(100);
    const allVisits = await visitsApi.getAll();
    return allVisits.filter(visit => visit.nurseId === nurseId);
  },

  async getByPatient(patientId: string): Promise<Visit[]> {
    await delay(100);
    const allVisits = await visitsApi.getAll();
    return allVisits.filter(visit => visit.patientId === patientId);
  },

  async checkIn(visitId: string, location: { latitude: number; longitude: number; address: string }): Promise<Visit | null> {
    return visitsApi.update(visitId, {
      status: 'in_progress',
      checkInTime: new Date(),
      location,
    });
  },

  async checkOut(visitId: string): Promise<Visit | null> {
    return visitsApi.update(visitId, {
      status: 'completed',
      checkOutTime: new Date(),
    });
  },
};

// Dashboard statistics
export const getDashboardStats = async () => {
  await delay(200);
  
  const today = new Date();
  today.setHours(0, 0, 0, 0);
  const tomorrow = new Date(today);
  tomorrow.setDate(tomorrow.getDate() + 1);

  const todayVisits = mockVisits.filter(visit => 
    visit.date >= today && visit.date < tomorrow
  );

  const activeNurses = mockNurses.filter(nurse => nurse.status === 'active');
  const openIncidents = mockIncidents.filter(incident => 
    ['open', 'investigating'].includes(incident.status)
  );

  return {
    totalPatients: mockPatients.length,
    activeNurses: activeNurses.length,
    visitsToday: todayVisits.length,
    completedVisitsToday: todayVisits.filter(v => v.status === 'completed').length,
    openIncidents: openIncidents.length,
    pendingBilling: mockBillingItems.filter(b => b.status === 'pending').length,
  };
};

